___CSS_LOADER_EXPORT___.push([module.id, `.aboutContent {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  padding-top: 100px;
}

.aboutContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 50px;

  padding-top: 100px;
}

.aboutRow {
  display: flex;
  flex-wrap: nowrap;
  gap: 50px;

  width: 60vw;
  background-color: var(--card);

  padding: 50px;
  border-radius: 0.8em;
}

.aboutContainer img {
  margin: auto;
  
  max-width: 100%; /* Set max-width instead of fixed width */
  max-height: 300px; /* Set max-height to limit the height */
  object-fit: cover;
}

.aboutContainer span {
  max-width: 100%; /* Set max-width for the text */
}

@media (max-width: 1500px) {
  .aboutRow {
    flex-wrap: wrap;
    gap: 20px;
    padding: 35px;

    width: 80vw;
  }
}

@media (max-width: 500px) {
  .aboutRow {
    flex-wrap: wrap;
    gap: 20px;
    padding: 20px;

    width: 90vw;
  }
}`, "",{"version":3,"sources":["webpack://./src/styles/about.css"],"names":[],"mappings":"AAAA;EACE,aAAa;EACb,sBAAsB;EACtB,uBAAuB;EACvB,mBAAmB;;EAEnB,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,uBAAuB;EACvB,SAAS;;EAET,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,iBAAiB;EACjB,SAAS;;EAET,WAAW;EACX,6BAA6B;;EAE7B,aAAa;EACb,oBAAoB;AACtB;;AAEA;EACE,YAAY;;EAEZ,eAAe,EAAE,yCAAyC;EAC1D,iBAAiB,EAAE,uCAAuC;EAC1D,iBAAiB;AACnB;;AAEA;EACE,eAAe,EAAE,+BAA+B;AAClD;;AAEA;EACE;IACE,eAAe;IACf,SAAS;IACT,aAAa;;IAEb,WAAW;EACb;AACF;;AAEA;EACE;IACE,eAAe;IACf,SAAS;IACT,aAAa;;IAEb,WAAW;EACb;AACF","sourcesContent":[".aboutContent {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n\n  padding-top: 100px;\n}\n\n.aboutContainer {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  gap: 50px;\n\n  padding-top: 100px;\n}\n\n.aboutRow {\n  display: flex;\n  flex-wrap: nowrap;\n  gap: 50px;\n\n  width: 60vw;\n  background-color: var(--card);\n\n  padding: 50px;\n  border-radius: 0.8em;\n}\n\n.aboutContainer img {\n  margin: auto;\n  \n  max-width: 100%; /* Set max-width instead of fixed width */\n  max-height: 300px; /* Set max-height to limit the height */\n  object-fit: cover;\n}\n\n.aboutContainer span {\n  max-width: 100%; /* Set max-width for the text */\n}\n\n@media (max-width: 1500px) {\n  .aboutRow {\n    flex-wrap: wrap;\n    gap: 20px;\n    padding: 35px;\n\n    width: 80vw;\n  }\n}\n\n@media (max-width: 500px) {\n  .aboutRow {\n    flex-wrap: wrap;\n    gap: 20px;\n    padding: 20px;\n\n    width: 90vw;\n  }\n}"],"sourceRoot":""}]);
