___CSS_LOADER_EXPORT___.push([module.id, `.contactContent {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  padding-top: 100px;
}

.contactContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 50px;

  padding-top: 100px;
}

.contactRow {
  display: flex;
  flex-wrap: nowrap;
  gap: 50px;

  width: 60vw;
  background-color: var(--card);

  padding: 50px;
  border-radius: 0.8em;
}

.contactTextContainer {
  display: flex;
  flex-direction: column;
}

.contactHeader {
  font-size: 30px;
}

.contactInfo {
  font-size: 28px;
  font-family: 'baseFont';
}

.contactContainer img {
  max-width: 300px;
  max-height: 100%; 
  object-fit: cover;
}

.contactContainer span {
  max-width: 100%;
}

@media (max-width: 1500px) {
  .contactRow {
    flex-wrap: wrap;
    gap: 20px;
    padding: 35px;

    width: 80vw;
  }
}

@media (max-width: 500px) {
  .contactRow {
    flex-wrap: wrap;
    gap: 20px;
    padding: 20px;

    width: 90vw;
  }

  .contactContainer img {
    margin: auto;
  }
}`, "",{"version":3,"sources":["webpack://./src/styles/contact.css"],"names":[],"mappings":"AAAA;EACE,aAAa;EACb,sBAAsB;EACtB,uBAAuB;EACvB,mBAAmB;;EAEnB,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,uBAAuB;EACvB,SAAS;;EAET,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,iBAAiB;EACjB,SAAS;;EAET,WAAW;EACX,6BAA6B;;EAE7B,aAAa;EACb,oBAAoB;AACtB;;AAEA;EACE,aAAa;EACb,sBAAsB;AACxB;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE,eAAe;EACf,uBAAuB;AACzB;;AAEA;EACE,gBAAgB;EAChB,gBAAgB;EAChB,iBAAiB;AACnB;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE;IACE,eAAe;IACf,SAAS;IACT,aAAa;;IAEb,WAAW;EACb;AACF;;AAEA;EACE;IACE,eAAe;IACf,SAAS;IACT,aAAa;;IAEb,WAAW;EACb;;EAEA;IACE,YAAY;EACd;AACF","sourcesContent":[".contactContent {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n\n  padding-top: 100px;\n}\n\n.contactContainer {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  gap: 50px;\n\n  padding-top: 100px;\n}\n\n.contactRow {\n  display: flex;\n  flex-wrap: nowrap;\n  gap: 50px;\n\n  width: 60vw;\n  background-color: var(--card);\n\n  padding: 50px;\n  border-radius: 0.8em;\n}\n\n.contactTextContainer {\n  display: flex;\n  flex-direction: column;\n}\n\n.contactHeader {\n  font-size: 30px;\n}\n\n.contactInfo {\n  font-size: 28px;\n  font-family: 'baseFont';\n}\n\n.contactContainer img {\n  max-width: 300px;\n  max-height: 100%; \n  object-fit: cover;\n}\n\n.contactContainer span {\n  max-width: 100%;\n}\n\n@media (max-width: 1500px) {\n  .contactRow {\n    flex-wrap: wrap;\n    gap: 20px;\n    padding: 35px;\n\n    width: 80vw;\n  }\n}\n\n@media (max-width: 500px) {\n  .contactRow {\n    flex-wrap: wrap;\n    gap: 20px;\n    padding: 20px;\n\n    width: 90vw;\n  }\n\n  .contactContainer img {\n    margin: auto;\n  }\n}"],"sourceRoot":""}]);
